import moment from 'moment';

export const isLocalHost = false;
const logDateError = false;
const amountToAdd = 3000;
export function getFlightObject(queryStringObj) {
    let flightObj = {
        from: '',
        to: '',
        departureDate: '',
        returnDate: '',
        directFlights: false,
        adults: 1,
        childs: 0,
        infants: 0,
        tripType: 0,
        cabinType: 5
    };
    if (Object.keys(queryStringObj).length) {
        let isDirectFlight = false;
        if (queryStringObj.directFlights !== undefined) {
            isDirectFlight = (queryStringObj.directFlights.toString().toLowerCase() === 'true');
        }
        flightObj.from = (queryStringObj.originLocation) ? queryStringObj.originLocation : '';
        flightObj.to = (queryStringObj.destLocation) ? queryStringObj.destLocation : '';
        flightObj.departureDate = (queryStringObj.departureDateTime) ? queryStringObj.departureDateTime : '';
        flightObj.returnDate = (queryStringObj.returnDateTime) ? queryStringObj.returnDateTime : '';
        flightObj.directFlights = isDirectFlight;
        flightObj.adults = (queryStringObj.adults) ? parseInt(queryStringObj.adults, 10) : 1;
        flightObj.childs = (queryStringObj.childs) ? parseInt(queryStringObj.childs, 10) : 0;
        flightObj.infants = (queryStringObj.infants) ? parseInt(queryStringObj.infants, 10) : 0;
        flightObj.tripType = (queryStringObj.tripType) ? parseInt(queryStringObj.tripType, 10) : 0;
        flightObj.cabinType = (queryStringObj.cabinType) ? parseInt(queryStringObj.cabinType, 10) : 5;
    }
    return flightObj;
}

export function getFlightsQueryString(object, url) {
    let requestParams = {
        "originLocation": object.from,
        "destLocation": object.to,
        "departureDateTime": object.departureDate,
        "returnDateTime": object.returnDate,
        "tripType": object.tripType,
        "directFlights": object.directFlights,
        "cabinType": object.cabinType,
        "adults": object.adults,
        "childs": object.childs,
        "infants": object.infants
    };
    if (object.departureDate === '') {
        delete requestParams['departureDateTime'];
    }
    if (object.returnDate === '') {
        delete requestParams['returnDateTime'];
    }
    if (object.tripType === "2") {
        delete requestParams['originLocation'];
        delete requestParams['destLocation'];
    }
    let queryString = Object.keys(requestParams).map(key => key + '=' + requestParams[key]).join('&');
    return url + "?" + queryString;
}

export function ensureTwoDigits(n) {
    return n > 9 ? "" + n : "0" + n;
}

export function formatMoney(amount, decimalCount = 0, decimal = ".", thousands = ",") {
    try {
        decimalCount = Math.abs(decimalCount);
        decimalCount = isNaN(decimalCount) ? 2 : decimalCount;

        const negativeSign = amount < 0 ? "-" : "";

        let i = parseInt(amount = Math.abs(Number(amount) || 0).toFixed(decimalCount)).toString();
        let j = (i.length > 3) ? i.length % 3 : 0;

        return negativeSign + (j ? i.substr(0, j) + thousands : '') + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousands) + (decimalCount ? decimal + Math.abs(amount - i).toFixed(decimalCount).slice(2) : "");
    } catch (e) {
        console.error("error in formatMoney");
        console.error(e)
    }
}

//#region Date manipulation functions
export function getDateFromString(dateString) {
    if(logDateError) console.info("Date 1");
    //dateString format is 26-Jan-2021
    if (dateString === null || dateString === undefined)
        return "";
    if (dateString.length === 11) {
        //format is 11-Jan-2021 change it to 2021-01-11
        dateString = getYYYYMMDDFormat(dateString) + "T00:00:00";
    }
    return moment("" + dateString).format('YYYY-MM-DD');
}

