export function getDifference(endDateTime, startDateTime) {
    if(logDateError) console.info("Date 11");
    if (endDateTime === null || endDateTime === undefined ||
        startDateTime === null || startDateTime === undefined)
        return "";
    /*if(endDateTime.length < 25 || startDateTime.length < 25)
        return "0";*/
    let nextMoment = moment(endDateTime);
    let prevMoment = moment(startDateTime);
    let difference = nextMoment.diff(prevMoment);
    let duration = moment.duration(difference);
    let hours = parseInt(duration.asHours(), 10);

// duration in minutes
    let minutes = parseInt(duration.asMinutes(), 10) % 60;
    return hours + "h " + minutes + "m";
}

export function getAgeInMonths(dateOfBirth) {
    if(logDateError) console.info("Date 11 dob");
    if (dateOfBirth === null || dateOfBirth === undefined)
        return "";
    /*if(endDateTime.length < 25 || startDateTime.length < 25)
        return "0";*/
    let nextMoment = moment();
    let prevMoment = moment(dateOfBirth, "DD-MM-YYYY");
    let difference = nextMoment.diff(prevMoment);
    let duration = moment.duration(difference);
    let months = parseInt(duration.asMonths(), 10);
    return ensureTwoDigits(months);
}

export function getMinutesToString(minutes) {
    if(logDateError) console.info("Date 11 b");
    let duration = moment.duration(minutes, 'minutes');
    let hours = parseInt(duration.asHours(), 10);

// duration in minutes
    let mins = parseInt(duration.asMinutes(), 10) % 60;
    return hours + "h " + mins + "m";
}

